import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AlertController, LoadingController, NavController, ToastController } from '@ionic/angular';
import { AccessProviders } from '../providers/access-providers';
import { Storage } from '@ionic/storage';
import { DataService } from '../data.service';
import { NavparamService } from '../navparam.service';


@Component({
  selector: 'app-transactions-consumer',
  templateUrl: './transactions-consumer.page.html',
  styleUrls: ['./transactions-consumer.page.scss'],
})
export class TransactionsConsumerPage implements OnInit {

  pen = true;
  rec = false;
  com = false;
  can = false;
  datastorage: any;
  name : any;
  id: any=0;

  order: any =[];
  item: any;
  img: any;

  constructor(
    private router: Router,
    private loadingController: LoadingController,
    private toastController: ToastController,
    private alertController: AlertController,
    private accessProviders: AccessProviders,
    public navController: NavController,
    private activatedRoute: ActivatedRoute,
    private storage: Storage,
    private navParamService: NavparamService,
    private data: DataService
  ) { }

  ngOnInit() {
    this.storage.get('storage_xxx').then((res)=>{
      console.log(res);
      this.order=[];
      this.id=res.user_id;
      console.log(this.id);
    return new Promise(resolve =>{
      let body = {
        aksi: 'get_orders_consumer',
        id: this.id
      }
      this.accessProviders.postData(body, 'get_orders.php').subscribe((res:any) => {
        if(res.success == true) {
           console.log(res.result);
           console.log(res.img);
           this.item=res.quant;
           this.img=res.img;
          for(let datas of res.result){ 
            this.order.push(datas);
          }  
        } else {
          this.presentToast('Failed'); 
        }
      });   
    });
  }); 
  }

  pending(){
    this.pen = true;
    this.rec = false;
    this.com = false;
    this.can = false;
  }
  received(){
    this.pen = false;
    this.rec = true;
    this.com = false;
    this.can = false;
  }
  completed(){
    this.pen = false;
    this.rec = false;
    this.com = true;
    this.can = false;
  }
  canceled(){
    this.pen = false;
    this.rec = false;
    this.com = false;
    this.can = true;
  }
  async presentToast(a) {
    const toast = await this.toastController.create({
      message: a,
      duration: 1500,
      position: 'top'
    });
    toast.present();
  }  
  view(e){
    this.navParamService.setNavData(e);
    this.navParamService.setNavData(this.id);
    this.navController.navigateRoot(['/order-status']);
  }
}
